.navigation {
    position: absolute;
    @include size(100%,70px);
    z-index: 2;
}


.brand {
    @include flex(flex,space-between,center,null);
    min-height: 70px;
}

.nav_main {
    @include flex(flex,center,center,null);

    &-list {
        @include flex(flex,center,center,null);

        &--items {
            position: relative;

            .nav-links {
                display: block;
                padding: 10px 20px;
                color: $color-white;
                text-decoration: none;


                &::before {
                    transition: 300ms;
                    content: "";
                    background-color: $color-cyan;
                    @include size(0%,5px);
                    @include position(absolute,38px,null,null,null)
                }

                &:hover::before {
                    width: 60%;
                }

                &:not(:only-child):after {
                    padding-left: 4px;
                    content: ' ▾';
                }

            }

